<template>
  <div class="seller" ref="seller">
      <div class="seller-content">
        <div class="overview">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc">
            <star :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}</span>分钟
              </div>
            </li>
          </ul>
          <div class="favorite" @click="toggleFavorite">
            <span class="icon-favorite" :class="{'active':favorite}"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="(item,index) in seller.supports">
              <span class="icon" :class="classMap[seller.supports[index].type]"></span>
              <span class="text">{{seller.supports[index].description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <h1 class="title">商家实景</h1>
          <div class="pic-wrapper" ref="picWrapper">
            <ul class="pic-list" ref="picList">
              <li class="pic-item" v-for="pic in seller.pics">
                <img :src="pic" width="120" height="90"/>
              </li>
            </ul>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title">商家信息</h1>
          <ul>
            <li class="info-item" v-for="info in seller.infos">
              {{info}}
            </li>
          </ul>
        </div>
      </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import Star from '../star/star'
  import Split from '../split/split'
  import {saveToLocal,loadFromLocal} from '../../common/js/store'
  export default {
    props: ['seller'] ,
    data() {
        return{
          favorite:(()=>{
              return loadFromLocal(this.seller.id,'favorite',false)
          })(),
          classMap:['decrease','discount','special','invoice','guarantee']
        }
    },
    created(){
      this.$nextTick(()=> { //加载完数据后初始化Scroll
        this.scroll = new BScroll(this.$refs.seller,{
          click:true
        })

        //每一张图片的宽度
        let picWidth = 120
        //每张力片的间隔
        let margin = 6
        //图片列表宽度
        let width = (picWidth+margin)*this.seller.pics.length-margin
        //给ul赋值总宽度
        this.$refs.picList.style.width = width + 'px'
        this.picScroll = new BScroll(this.$refs.picWrapper,{
          //可以设置是否显示横向或纵向滚动条
          scrollX:true,
          //使用IScroll的横轴滚动时，如想使用系统立轴滚动并在横轴上生效，请开启
          eventPassthrough:'vertical'
        })
      })
    },
    computed:{
      favoriteText(){
          return this.favorite ? '已收藏':'收藏'
      }
    },
    methods:{
      toggleFavorite(event){
          if(!event._constructed){
              return
          }
          this.favorite = !this.favorite
          saveToLocal(this.seller.id,'favorite',this.favorite)
      }
    },
    components:{
        Star,
        Split
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .seller
    position:absolute
    top: 174px
    bottom:0
    left:0
    width:100%
    overflow:hidden
    .seller-content
      .overview
        position:relative
        padding: 18px
        .title
          margin-bottom:8px
          line-height:14px
        .desc
          padding-bottom:18px
          border-bottom-1px(rgba(7,17,27,0.1))
          font-size:0
          .star
            display:inline-block
            margin-right:8px
          .text
            display:inline-block
            line-height:18px
            margin-right:12px
            vertical-align:top
            font-size:10px
            color:rgb(7,17,27)

        .remark
          display:flex
          padding-top:18px
          .block
            flex:1
            text-align:center
            border-right:1px solid rgba(7,17,27,0.1)
            &:last-child
              border:none
            h2
              margin-bottom: 4px
              line-height:10px
              font-size:10px
              color:rgb(147,153,159)
            .content
              line-height: 24px
              font-size: 10px
              color:rgb(7,17,27)
              .stress
                font-size:24px
        .favorite
          position:absolute
          width:50px
          right: 5px
          top: 18px
          text-align:center
          .icon-favorite
            display:block
            line-height:24px
            font-size:24px
            color:#d4d6d9
            &.active
              color:rgb(240,20,20)
          .text
            line-height: 10px
            font-size: 10px
            color:rgb(77,85,93)
      .bulletin
        padding:18px 18px 0 18px
        .title
          margin-bottom:8px
          line-height:14px
          color:rgb(7,17,27)
        .content-wrapper
          padding:0 12px 16px 12px
          border-bottom-1px(rgba(7,17,27,0.1))
          .content
            line-height: 24px
            font-size:12px
            color:rgb(240,20,20)

        .supports
          .support-item
            padding:16px 12px
            border-bottom-1px(rgba(7,17,27,0.1))
            font-size:0
            &:last-child
              border:none
            .icon
              display:inline-block
              vertical-align: top
              width:16px
              height: 16px
              margin-right:4px
              background-size:16px 16px
              background-repeat: no-repeat
              &.decrease
                bg-image('decrease_4')
              &.discount
                bg-image('discount_4')
              &.guarantee
                bg-image('guarantee_4')
              &.special
                bg-image('special_4')
              &.invoice
                bg-image('invoice_4')
            .text
              line-height: 16px
              font-size: 12px
              color:rgb(7,17,27)
      .pics
        padding:18px
        .title
          margin-bottom: 12px
          line-height: 14px
          color:rgb(7,17,27)
          font-size:14px
        .pic-wrapper
          width:100%
          overflow:hidden
          white-space:nowrap //不换行
          .pic-list
            font-size:0
            .pic-item
              display:inline-block
              margin-right: 6px
              width: 120px
              height:90px
              &:last-child
                margin:0

      .info
        padding:18px 18px 0 18px
        .title
          padding-bottom: 12px
          line-height:14px
          border-bottom-1px(rgba(7,17,27,0.1))
          color:rgb(7,17,27)
          font-size:14px
        .info-item
          padding:16px 12px
          line-height:16px
          color:rgb(7,17,27)
          fot-size:12px
          &:last-child
            border:none
</style>



