<template>
  <div >
    <page-header v-bind:seller="seller"></page-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>

  </div>

</template>

<script>
import header from './components/header/header'
const ERROR_NO = 0
export default {
  data(){
    return {
        seller:{}
    }
  } ,
  created(){
    this.$ajax.get('seller').then((res)=>{
      if(res.data.errno === ERROR_NO){
          this.seller = res.data.data
      }
      console.log(this.seller)
    })
  },
  components:{
      'page-header':header
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "./common/stylus/mixin.styl"
    .tab
      display:flex
      width:100%
      height: 40px
      line-height 40px
      border-top-1px(rgba(7,17,27,0.1))
      border-bottom-1px(rgba(7,17,27,0.1))
      .tab-item
        flex:1
        text-align:center
        & > a
          display:block
          font-size: 14px
          color:rgb(77,85,93)
          &.router-link-active
            color:rgb(240,20,20)
</style>



